@font-face {
  font-family: Cascadia Tablature;
  src: url("CascadiaTablature.ttf") format('truetype');
}
@font-face {
  font-family: Cascadia Mono;
  src: url("CascadiaMono.ttf") format('truetype');
}

:root {
  --shadow:rgba(0, 0, 0, 0.2);

  --bg0: #FEF8EC;
  --bg1:#F9F1E1;
  --bg2:#F0E9D6;
  --bg3:#F2E7CA;
  --hover0:#D1DFD0;
  --hover1:#D2F3F9;
  --fg0:#21a0a0;
  --fg1:#046865;
  --fg2:#46b1c9;
  --accent0:#d7263d;
  --accent1:#f87060;
  --accent2:#f97d6e;
  
  --title-width: 360px;
  --controls-max-width: 475px;
  --tablature-font-size: 13px;
}

/* max-width must be larger that sum of --title-width and --controls-max-width */
@media only screen and (max-width: 860px) {
  .optional {
    display: none;
  }
  :root {
    --tablature-font-size: 10px;
  }
}

body {
  border: 0;
  margin: 0;
  padding: 0;
  overflow: hidden;
  scrollbar-color: var(--hover0) var(--bg0);
}

.main {
  font-family: Cascadia Mono;
  height: 100vh;
  border: 0;
  margin: 0;
  padding: 0;
  display: grid;
  grid-template-rows: 40px auto;
  background-color: var(--bg1);
}

.header {
  display: flex;
  flex-wrap: wrap;
  z-index: 100;
  margin: 0;
  border: 0;
  box-shadow: 0 0 6px 0 var(--shadow);
  justify-content: center;
}
.title {
  margin: auto 0;
  padding: 0 10px;
  width: var(--title-width);
}
.title a {
  text-decoration: none;
}
.title h1 {
  margin: auto auto;
  color:var(--fg0);
  font-family: Cascadia Mono;
  font-size: 24px;
}

.controls {
  display: flex;
  margin: 0;
  border: 0;
  max-width: var(--controls-max-width);
}
.controls button {
  padding: 10px 32px;
  height: 100%;
  margin: 0 1.5px;
  border: 0;
  background-color: var(--bg2);
  font-family: Cascadia Mono;
  font-size: 13px;
  color:var(--fg0);
  cursor: pointer;
  box-shadow: 0 0 4px 0 var(--shadow);
}
.controls button:hover {
  background-color: var(--hover0);
}

.tablatureViewer {
  background-color:var(--bg1);
  grid-row-start: 2;
  overflow: auto;
}
.tablatureViewer pre {
  margin: 0;
  border: 0;
  padding: 10px 0 0 10px;
  font-family: Cascadia Tablature;
  font-size: var(--tablature-font-size);
  color:var(--fg0);
  overflow: auto;
}

.tablatureEditor {
  display: flex;
  flex-direction: column;
  grid-row-start: 2;
}
.tablatureEditor textarea {
  white-space: pre;
  background-color:var(--bg0);
  height: 100%;
  margin: 0;
  border: 0;
  padding: 10px 0 0 10px;
  outline: none;
  resize: none;
  font-family: Cascadia Tablature;
  font-size: var(--tablature-font-size);
}
